/* Tablets */
@media only screen and (min-device-width : 768px) and (max-device-width: 1224px) {
  html {
    font-size: 16px!important;
  }
  ::-webkit-scrollbar {
    width: 0px;
  }
}
/* Mobile */
@media only screen and (max-device-width : 480px) {
  html {
    font-size: 14px!important;
  }
  ::-webkit-scrollbar {
    width: 0px;
  }
}
